    orphans: 3;
    widows: 3;
  }
  h2, h3 {
    page-break-after: avoid;
  }
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  max-width: 1140px;
  margin: 0 auto;
}

.row {
  margin-left: -15px;
  margin-right: -15px;
}

.row [class^="col-"] {
  box-sizing: border-box;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 30px;
}

@media (min-width: 768px) {
  .row {
    display: flex;
  }
  .row .col-1-12 {
    width: 8.33333%;
  }
  .row .col-2-12 {
    width: 16.66667%;
  }
  .row .col-3-12 {
    width: 25%;
  }
  .row .col-4-12 {
    width: 33.33333%;
  }
  .row .col-5-12 {
    width: 41.66667%;
  }
  .row .col-6-12 {
    width: 50%;
  }
  .row .col-7-12 {
    width: 58.33333%;
  }
  .row .col-8-12 {
    width: 66.66667%;
  }
  .row .col-9-12 {
    width: 75%;
  }
  .row .col-10-12 {
    width: 83.33333%;
  }
  .row .col-11-12 {
    width: 91.66667%;
  }
  .row .col-12-12 {
    width: 100%;
  }
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Nunito Sans', sans-serif;
  color: #383838;
}

html {
  scroll-behavior: smooth;
}

section {
  padding-top: 35px;
  padding-bottom: 20px;
}

.page-center.container {
  margin-top: 60px;
  margin-bottom: 80px;
}

.section_header {
  position: relative;
  margin-bottom: 50px;
  text-transform: uppercase;
  color: #333333;
  display: inline-block;
}

.section_header::after {
  content: "";
  height: 2px;
  width: 110%;
  background-color: #30aebe;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
}

.section_header h2 {
  font-size: 28px;
  margin: 0;
  padding: 0;
}

.header_container {
  text-align: center;
}

.button,
.wpcf7-form-control.wpcf7-submit {
  cursor: pointer;
  padding: 10px 60px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  background-color: #30aebe;
  border: 1px solid #30aebe !important;
  border-radius: 15px;
  color: #30aebe;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.6px;
  box-shadow: inset 0 0 0 60px #fafafa;
  transition: 0.5s;
}

.button:hover,
.wpcf7-form-control.wpcf7-submit:hover {
  box-shadow: inset 0 0 0 0px #fafafa;
  color: #fafafa;
  outline: none;
}

.wpcf7-form-control.wpcf7-submit {
  padding: 10px 50px;
  margin-top: 5px;
}

.banner {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  height: 175px;
  background: linear-gradient(170deg, #eff8ff 0%, #c8f5fa 33%, #75e5f3 53%, #33c5ec 73%, #5cb0ee 100%);
  margin-bottom: 80px;
}

.banner_text.container {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 600;
  line-height: 150%;
}

.box {
  max-width: 310px;
  margin-left: auto;
  margin-right: auto;
}

.box_photo {
  text-decoration: none;
}

.box_photo_container {
  height: 207px;
  overflow: hidden;
}

.box_photo img {
  display: block;
  max-width: 100%;
  height: auto;
  transition: 2s;
}

.box:hover .box_photo img {
  transform: scale(1.2);
}

.box_tittle {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  color: #333333;
  margin-top: 20px;
  margin-bottom: 0;
}

.box:hover .box_tittle {
  color: #30aebe;
}

.page_header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.page_hero .page_header {
  width: 100%;
}

.page_header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.contact_nav {
  width: 100%;
}

.contact_nav_list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.contact_nav_list li {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  font-size: 12px;
}

.contact_nav_list img {
  display: block;
  width: 20px;
  margin-right: 5px;
  height: auto;
}

.contact_menu::after {
  display: block;
  content: "";
  height: 1px;
  background-color: #999999;
}

.header_logo {
  display: flex;
  align-items: center;
  max-width: 300px;
}

.header_logo h1 {
  margin: 0px;
  margin-top: 8px;
}

.page_logo img {
  width: 145px;
  height: auto;
}

.NFZ_logo img {
  margin-left: 14px;
  height: auto;
  width: 80px;
}

.page_nav_list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.page_nav_list li {
  padding: 15px 7px;
}

.page_nav_list a {
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  color: #333333;
  font-size: 18px;
  font-weight: 800;
  display: block;
  transition: 0.3s color;
}

.page_nav_list a::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 1px;
  background: #30aebe;
  left: 50%;
  right: 50%;
  opacity: 0;
  transition: 0.5s left, 0.5s right, 0.5s opacity;
}

.page_nav_list a:hover {
  color: #30aebe;
}

.page_nav_list a:hover::after {
  left: 0;
  right: 0;
  opacity: 1;
}

.page_nav_sublist {
  display: none;
  z-index: 10000;
}

.page_nav_list_button {
  display: none;
  padding-left: 2px;
  font-size: 20px;
}

.toggle-main-nav {
  display: none;
  right: 10px;
  top: 7px;
  width: 60px;
  height: 60px;
  background: none;
  border: 0;
  cursor: pointer;
}

.toggle-main-nav span {
  display: block;
  background: #333333;
  height: 6px;
  width: 80%;
  margin: 5px auto;
  border-radius: 2px;
  transition: 0.5s;
}

.toggle-main-nav:focus {
  outline: none;
}

.page_nav.home {
  display: none;
}

@media (max-width: 760px) {
  .toggle-main-nav {
    display: block;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 10000;
  }
  .toggle-main-nav:hover span {
    background: #2ea7b6;
  }
  .header_logo {
    margin: 0 auto;
  }
  .page_nav_sublist.show {
    display: block;
    padding: 20px 0 0 20px;
    z-index: 10000;
  }
  .page_nav_sublist.show li {
    border-bottom: none;
  }
  .page_nav_sublist.show a {
    font-size: 14px;
    text-align: left;
  }
  .page_nav_list_button {
    display: inline-block;
  }
  .page_hero .page_nav {
    display: none;
  }
  .page_nav.home {
    display: block;
  }
  .page_nav {
    position: fixed;
    top: 0;
    left: -320px;
    width: 300px;
    height: 100vh;
    background: white;
    transition: 1s left;
    box-shadow: 1px 0 10px rgba(0, 0, 0, 0.4);
    z-index: 1000000;
  }
  .page_nav.show {
    left: 0;
  }
  .page_nav li {
    display: block;
    text-align: center;
    border-bottom: 1px solid #b3b3b3;
  }
  .page_nav_list {
    flex-direction: column;
    justify-content: center;
  }
  .page_nav_list a::after {
    display: none;
  }
}

.page_header.home {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1;
}

.page_hero {
  height: 100vh;
  position: relative;
}

.slider,
.slider {
  height: 100vh;
  width: 100%;
  background-size: cover;
  background-position: center;
}

.main_banner_addon {
  position: absolute;
  bottom: -1px;
  left: 50%;
  transform: translate(-50%);
  height: 40px;
}

.main_banner_arrow {
  position: absolute;
  height: 20px;
  bottom: 1.2%;
  left: 50%;
  transform: translate(-50%);
}

.section_sevices {
  min-height: 100vh;
}

.section_aboutUs {
  padding-top: 0px;
  min-height: 100vh;
}

.section_aboutUs .box {
  max-width: 400px;
}

.section_aboutUS_photo {
  max-width: 100%;
  height: auto;
  display: block;
}

.section_aboutUs .section_header {
  margin-bottom: 20px;
}

.section_aboutUs .header_container {
  text-align: left;
  margin-left: 50px;
}

.section_aboutUs_article {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  height: 100%;
}

.section_aboutUs_text {
  line-height: 150%;
  margin-top: 0px;
}

#contact {
  padding-top: 20px;
  min-height: 100vh;
}

#contact .section_header {
  margin-bottom: 0px;
}

label {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

label .focusable:active,
label .focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.contact_data textarea {
  resize: none;
}

.contact_data input,
.contact_data textarea {
  padding: 12px;
  border: 1px solid #C4C4C4;
  font-family: inherit;
  border-radius: 7px;
  background-color: #F9F9F9;
}

.contact_data input:focus,
.contact_data textarea:focus {
  border-color: #333;
  outline: none;
}

.g-recaptcha {
  transform: scale(0.7);
  transform-origin: 0 0;
}

.submit_container {
  display: flex;
  margin-top: 20px;
  padding-left: 30px;
}

.col-12-12.rodo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 0;
}

.wpcf7-list-item input {
  width: 18px;
  height: 18px;
}

.rodo p {
  font-size: 14px;
  padding-left: 10px;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #EBECF1 inset !important;
}

.rodo a {
  font-weight: 600;
  color: #333333;
}

div.wpcf7-response-output {
  border: none !important;
  color: #ff0000;
  margin-top: 0 !important;
}

div.wpcf7-mail-sent-ok {
  color: #30aebe;
  border: none !important;
  margin-top: 0 !important;
}

div.wpcf7-mail-sent-ng,
div.wpcf7-aborted {
  color: #ff0000;
  border: none !important;
  margin-top: 0 !important;
}

div.wpcf7-spam-blocked {
  color: #ff0000;
  border: none !important;
  margin-top: 0 !important;
}

div.wpcf7-validation-errors,
div.wpcf7-acceptance-missing {
  color: #ff0000;
  border: none !important;
  margin-top: 0 !important;
}

.use-floating-validation-tip span.wpcf7-not-valid-tip {
  border: none !important;
  color: #ff0000;
  margin-top: 0 !important;
}

.contact_adress {
  margin-top: 50px;
}

.contact_adress .col-1-12 {
  padding-right: 0px;
}

.contact_address.address {
  display: block;
  width: 40px;
  height: auto;
}

.address_address strong:nth-of-type(2) {
  text-decoration: underline;
}

.contact_address.mail,
.contact_address.job,
.contact_address.fb {
  display: block;
  width: 35px;
  height: auto;
  margin-right: 15px;
}

.image_box {
  display: flex;
  justify-content: flex-end;
  padding-top: 5px;
}

.address_box {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.address_address,
.address_hours,
.address_phone {
  margin-top: 5px;
  margin-bottom: 5px;
}

.mail_link,
.fb_link,
.job_link {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: #333333;
  text-decoration: none;
  font-weight: 600;
}

.mail_box {
  margin-top: 5px;
  margin: 0 auto;
  width: 260px;
}

.mail_link {
  margin-bottom: 15px;
}

.job_box {
  margin-top: 5px;
  margin: 0 auto;
  width: 150px;
}

.main_map {
  height: 500px;
}

.footer {
  background: linear-gradient(170deg, #eff8ff 0%, #c8f5fa 33%, #75e5f3 53%, #33c5ec 73%, #5cb0ee 100%);
  padding-top: 25px;
}

.footer_nav_list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer_nav_list li {
  height: 20px;
}

.footer_nav_list li a {
  text-decoration: none;
  text-transform: uppercase;
  color: #333333;
  font-size: 12px;
